.card-child {
    height: 100px;
    padding-left: 1px;
    padding-right: 1px;
    background-color: #bae7ff;
    border: 1px solid #1890ff;
    margin-bottom: 2px;
    margin-right: 2px;
    cursor: pointer;
    line-height: 100px;
    color: #333;
    font-size: 32px;
    text-align: center;
}

.card-child:hover {
    background-color: #91d5ff;
}

.card-child.show {
    background-color: #adc6ff;
    border: 1px solid #2f54eb;
}
